import React, { useRef, useState, useEffect } from "react";
import type { MouseEvent, KeyboardEvent } from "react";
import { motion, AnimatePresence } from "framer-motion";

import commandHelps, { suggestions } from "../data/outputs";
const availableCommands = Object.keys(commandHelps);

export const Terminal = () => {
  const terminal = useRef<HTMLDivElement>(null);
  const textareaRef = useRef<HTMLDivElement>(null);
  const [open, setOpen] = useState(false);
  const [history, setHistory] = useState<string[]>(["/help"]);
  const [validCommandsHistory, setValidCommandsHistory] = useState<string[]>(
    []
  );
  const [options, setOptions] = useState<string[]>([]);

  const historyCursor = useRef<number>(0);

  useEffect(() => {
    try {
      const history = window.localStorage.getItem("validCommandsHistory");
      if (history) {
        setValidCommandsHistory(JSON.parse(history));
      }
    } catch (e) {
      // ignore
    }
  }, []);

  useEffect(() => {
    try {
      window.localStorage.setItem(
        "validCommandsHistory",
        JSON.stringify(validCommandsHistory.slice(-100))
      );
    } catch (e) {
      // ignore
    }
  }, [validCommandsHistory]);

  useEffect(() => {
    if (!terminal.current) return;
    terminal.current.addEventListener("fullscreenchange", () => {
      if (!document.fullscreenElement) {
        if (!terminal.current) return;
        terminal.current.classList.remove("fullscreen");
      }
    });
  }, [terminal.current]);

  const openTerminal = () => {
    setOpen(true);
  };

  const closeTerminal = () => {
    setOpen(false);
  };

  const minimizeTerminal = () => {
    setOpen(false);
  };

  const maximizeTerminal = () => {
    if (!terminal.current) return;
    terminal.current.requestFullscreen();
    terminal.current.classList.add("fullscreen");
  };

  const grabFocus = () => {
    if (!textareaRef.current) return;
    textareaRef.current.parentElement?.scrollTo(0, 0);
    textareaRef.current.focus();
  };

  function handleCommandChange(e: KeyboardEvent<HTMLDivElement>) {
    e.cancelable = true;
    if (!textareaRef.current) return;
    const key = e.key;
    if (key === "Enter") {
      e.preventDefault();
      if (textareaRef.current.innerText[0] !== "/") {
        setHistory((prev) => [
          ...prev,
          "<br><s>start command with <b>/</b> (backslash)</s>",
        ]);
        textareaRef.current.innerText = "";
        return;
      }
      handleSubmit(textareaRef.current.innerText.toLowerCase());
      textareaRef.current.innerText = "";
      historyCursor.current = 1;
    } else if (key === "ArrowUp") {
      e.preventDefault();
      if (historyCursor.current > validCommandsHistory.length - 1) {
        historyCursor.current = 0;
      }
      historyCursor.current += 1;
      textareaRef.current.innerText =
        validCommandsHistory[
          validCommandsHistory.length - historyCursor.current
        ] || "";
    } else if (key === "ArrowDown") {
      e.preventDefault();
      historyCursor.current -= 1;
      if (historyCursor.current <= 0) {
        historyCursor.current = 0;
        textareaRef.current.innerText = "";
        return;
      }
      textareaRef.current.innerText =
        validCommandsHistory[
          validCommandsHistory.length - historyCursor.current
        ] || "";
    } else if (e.metaKey || e.ctrlKey) {
      if (key === "c") {
        textareaRef.current.innerText = "";
        closeTerminal();
      }
      if (key === "l") {
        textareaRef.current.innerText = "";
        setHistory([]);
      }
    } else {
      let innerText = textareaRef.current.innerText;
      if (key === "Backspace") {
        innerText = innerText.slice(0, -1);
      } else if (key.length === 1) {
        innerText = textareaRef.current.innerText + key;
      }
      const tokens = innerText
        .split(" ")
        .map((token) => token.trim())
        .filter((token) => token.length > 0);
      if (tokens.length === 1 && suggestions[tokens[0]]) {
        setOptions(suggestions[tokens[0]]);
      } else {
        setOptions([]);
      }
    }
  }

  function openLink(url: string, showMessage = true) {
    if (showMessage) {
      setHistory((prev) => [
        ...prev,
        "<br><strong>Opening</strong> " + url + "...",
      ]);
    }
    setTimeout(() => {
      window.open(url);
    }, 1000);
  }

  function handleSubmit(command: string) {
    const tokens = command
      .split(" ")
      .map((token) => token.trim())
      .filter((token) => token.length > 0);
    if (!availableCommands.includes(tokens[0])) {
      setHistory((prev) => [
        ...prev,
        "<s><b>" + command + "</b> command not found</s>",
      ]);
      return;
    } else if (tokens.length > 2) {
      setHistory((prev) => [
        ...prev,
        "<br><b>" +
          tokens[0] +
          "</b><br>&emsp; unknown option <s>" +
          tokens.slice(1).join(" ") +
          "</s>",
      ]);
    }

    if (tokens[0] === "/exit") {
      setValidCommandsHistory((prev) => [...prev, "/exit"]);
      closeTerminal();
    } else if (tokens[0] === "/clear") {
      setHistory([]);
      setValidCommandsHistory((prev) => [...prev, "/clear"]);
    } else if (tokens[0] === "/help") {
      if (tokens.length === 2) {
        if (availableCommands.includes(tokens[1])) {
          setHistory((prev) => [...prev, tokens[1]]);
          setValidCommandsHistory((prev) => [...prev, tokens[1]]);
        } else {
          setHistory((prev) => [
            ...prev,
            "<br><b>/help</b><br>&emsp; unknown option <s>" +
              tokens[1] +
              "</s>",
          ]);
        }
      } else if (tokens.length === 1) {
        setHistory((prev) => [...prev, "/help"]);
        setValidCommandsHistory((prev) => [...prev, "/help"]);
      }
    } else if (tokens[0] === "/contact") {
      const opt = tokens[1].toUpperCase();
      if (suggestions["/contact"].includes(opt)) {
        setHistory((prev) => [...prev, "<br><strong>/contact</strong> " + opt]);
        setValidCommandsHistory((prev) => [...prev, "/contact " + opt]);
        if (opt === "-E" || opt === "EMAIL") {
          openLink("mailto:hi@shakil.info", false);
        } else if (opt === "-W" || opt === "WHATSAPP") {
          openLink("https://wa.me/+8801861590250");
        } else if (opt === "-M" || opt === "MESSENGER") {
          openLink("https://m.me/sxakil");
        }
      } else {
        setHistory((prev) => [
          ...prev,
          "<br><strong>/contact</strong><br>&emsp; unknown option <s>" +
            opt +
            "</s>",
        ]);
      }
    } else if (tokens[0] === "/visit") {
      const opt = tokens[1].toUpperCase();
      if (suggestions["/visit"].includes(opt)) {
        setHistory((prev) => [...prev, "<br><strong>/visit</strong> " + opt]);
        setValidCommandsHistory((prev) => [...prev, "/visit " + opt]);
        if (opt === "-G" || opt === "GITHUB") {
          openLink("https://github.com/sXakil");
        } else if (opt === "-L" || opt === "LINKEDIN") {
          openLink("https://linkedin.com/in/sxakil");
        } else if (opt === "-X" || opt === "TWITTER") {
          openLink("https://x.com/shakil_codes");
        } else if (opt === "-I" || opt === "INSTAGRAM") {
          openLink("https://instagram.com/sxakil");
        }
      } else {
        setHistory((prev) => [
          ...prev,
          "<br><b>/visit</b><br>&emsp; unknown option <s>" + opt + "</s>",
        ]);
      }
    } else if (tokens[0] === "/download") {
      const opt = tokens[1].toUpperCase();
      if (suggestions["/download"].includes(opt)) {
        setHistory((prev) => [
          ...prev,
          "<br><strong>/download</strong> " + opt,
        ]);
        setValidCommandsHistory((prev) => [...prev, "/download " + opt]);
        if (opt === "-C" || opt === "CV") {
          openLink("https://shakil.info/Shakil-CV.pdf");
        } else if (opt === "-P" || opt === "PORTRAIT") {
          openLink("https://shakil.info/shakil.png");
        }
      } else {
        setHistory((prev) => [
          ...prev,
          "<br><b>/visit</b><br>&emsp; unknown option <s>" + opt + "</s>",
        ]);
      }
    } else if (tokens[0] === "/destroy") {
      setHistory((prev) => [...prev, "<br><strong>/destroy</strong>"]);
      setValidCommandsHistory((prev) => [...prev, "/destroy"]);
      window.location.href = "https://www.youtube.com/watch?v=dQw4w9WgXcQ";
    }
  }

  function handleOptionClick(e: MouseEvent<HTMLButtonElement>) {
    e.stopPropagation();
    if (!textareaRef.current) return;
    setOptions([]);
    const opt = e.currentTarget.innerText;
    const innerText = textareaRef.current.innerText.trim();
    textareaRef.current.innerText = [innerText, opt].join(" ");
    grabFocus();
  }

  return (
    <>
      <AnimatePresence>
        {open && (
          <motion.div
            initial={{ opacity: 0, scale: 0 }}
            animate={{
              opacity: 1,
              scale: 1,
            }}
            exit={{ opacity: 0, scale: 0 }}
            className="backdrop-mask"
            role="button"
            onClick={closeTerminal}
          ></motion.div>
        )}
      </AnimatePresence>
      <AnimatePresence>
        {open && (
          <motion.div
            initial={{
              opacity: 0,
              scale: 0,
              x: "-250%",
              y: "250%",
            }}
            animate={{
              opacity: 1,
              scale: 1,
              x: "-50%",
              y: "-50%",
            }}
            exit={{ opacity: 0, scale: 0, x: "-250%", y: "250%" }}
            className="terminal"
            // style={{ originX: 0, originY: 0 }}
            ref={terminal}
          >
            <div className="terminal-header">
              <div className="terminal-btns">
                <button className="close" onClick={closeTerminal}>
                  <span>&times;</span>
                </button>
                <button className="minimize" onClick={minimizeTerminal}>
                  <span>&minus;</span>
                </button>
                <button className="maximize" onClick={maximizeTerminal}>
                  <span>+</span>
                </button>
              </div>
            </div>
            <div className="terminal-body">
              <div
                ref={textareaRef}
                className="textarea"
                data-gramm="false"
                contentEditable
                suppressContentEditableWarning
                onKeyDown={handleCommandChange}
              ></div>
              <div
                className="history"
                role="button"
                onClick={grabFocus}
                onKeyDown={grabFocus}
              >
                {options.length > 0 && (
                  <div className="suggestion">
                    {options.map((opt, index) => (
                      <button key={index} onClick={handleOptionClick}>
                        {opt}
                      </button>
                    ))}
                  </div>
                )}
                <AnimatePresence mode="wait" initial={false}>
                  {history.map((cmd, index) => (
                    <motion.div
                      initial={{ scale: 0.8, opacity: 0 }}
                      animate={{ scale: 1, opacity: 1 }}
                      exit={{ scale: 0.8, opacity: 0 }}
                      transition={{ type: "tween" }}
                      className="line"
                      key={index}
                      dangerouslySetInnerHTML={{
                        __html: commandHelps[cmd] || cmd,
                      }}
                    ></motion.div>
                  ))}
                </AnimatePresence>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      <AnimatePresence>
        {!open && (
          <motion.button
            className="terminal-btn"
            onTap={openTerminal}
            initial={{ y: "100%" }}
            animate={{ y: 0 }}
            exit={{ y: "100%" }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="16"
              fill="#fff"
              viewBox="0 0 16 16"
            >
              <path d="M6 9a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 0 1h-3A.5.5 0 0 1 6 9M3.854 4.146a.5.5 0 1 0-.708.708L4.793 6.5 3.146 8.146a.5.5 0 1 0 .708.708l2-2a.5.5 0 0 0 0-.708z" />
              <path d="M2 1a2 2 0 0 0-2 2v10a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V3a2 2 0 0 0-2-2zm12 1a1 1 0 0 1 1 1v10a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V3a1 1 0 0 1 1-1z" />
            </svg>
            <span>Terminal</span>
          </motion.button>
        )}
      </AnimatePresence>
    </>
  );
};
